import { useParams } from "react-router-dom";

const BASE_URL = `https://api.coinpaprika.com/v1`;

//json data의 promise로 async 함수로도 만들 수 있음
/* 
export async function fetchCoins() {
  return fetch(`${BASE_URL}/coins`).then((response) => response.json());
}
*/

export function fetchCoins() {
  return fetch(`${BASE_URL}/coins`).then((response) => response.json());
}

export function fetchInfo(coinId: string) {
  return fetch(`${BASE_URL}/coins/${coinId}`).then((res) => res.json());
}

export function fetchPrice(coinId: string) {
  return fetch(`${BASE_URL}/tickers/${coinId}`).then((res) => res.json());
}

export function fetchCoinHistory(coinId: string) {
  const endDate = Math.floor(Date.now() / 1000);
  const stDate = endDate - 60 * 60 * 24 * 7 * 2;
  return fetch(
    `${BASE_URL}/coins/${coinId}/ohlcv/historical?start=${stDate}&end=${endDate}`
  ).then((res) => res.json());
}
