import { useEffect, useState } from "react";
import { useQuery } from "react-query";
import moment from "moment";
import { fetchPrice } from "../api";
import styled from "styled-components";
import { useParams } from "react-router-dom";
import { Params, IPriceData } from "../atoms";

const PriceView = styled.div`
  display: flex;
  justify-content: space-between;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 10px 20px;
  border-radius: 10px;
`;

const PriceViewItem = styled.span`
  display: flex;
  flex-direction: column;
  align-items: center;

  span:first-child {
    font-size: 13px;
    font-weight: 400;
    text-transform: uppercase;
    margin-bottom: 5px;
  }
`;

interface priceProps {
  coinId: string;
}

function Price() {
  const { coinId } = useParams() as Params;
  const [color, setColor] = useState(Boolean);
  const { isLoading, data } = useQuery<IPriceData>(
    ["priceTickers", coinId],
    () => fetchPrice(coinId),
    {
      refetchInterval: 5000,
    }
  );
  return (
    <div>
      {isLoading ? (
        "Loading price..."
      ) : (
        <>
          <PriceView>
            <PriceViewItem>${data?.quotes.USD.price.toFixed(3)}</PriceViewItem>
            <PriceViewItem>
              {PriceColor(data?.quotes.USD.market_cap_change_24h as number)}
            </PriceViewItem>
            <PriceViewItem>
              <span>
                갱신 시간:
                {moment(data?.last_updated).format("YYYYMMDD HH:mm:ss")}
              </span>
            </PriceViewItem>
          </PriceView>
          <PriceView>
            <PriceViewItem>
              <span>1시간</span>
              <span>
                {PriceColor(data?.quotes.USD.percent_change_1h as number)}
              </span>
            </PriceViewItem>
            <PriceViewItem>
              <span>24시간</span>
              <span>
                {PriceColor(data?.quotes.USD.percent_change_24h as number)}
              </span>
            </PriceViewItem>
            <PriceViewItem>
              <span>주간</span>
              <span>
                {PriceColor(data?.quotes.USD.percent_change_7d as number)}
              </span>
            </PriceViewItem>
            <PriceViewItem>
              <span>월간</span>
              <span>
                {PriceColor(data?.quotes.USD.percent_change_30d as number)}
              </span>
            </PriceViewItem>
            <PriceViewItem>
              <span>년간</span>
              <span>
                {PriceColor(data?.quotes.USD.percent_change_1y as number)}
              </span>
            </PriceViewItem>
          </PriceView>
        </>
      )}
    </div>
  );
}

export default Price;

function PriceColor(price: number) {
  return Math.sign(price) > 0 ? (
    <span style={{ color: "#43aa05" }}>+{price.toFixed(2)}%</span>
  ) : (
    <span style={{ color: "#e15241" }}>{price.toFixed(2)}%</span>
  );
}
