import { useParams } from "react-router";
import { useLocation, Outlet, useMatch } from "react-router-dom";
import { Helmet } from "react-helmet";
import {
  Container,
  Description,
  Header,
  Loader,
  Overview,
  OverviewItem,
  Tab,
  Tabs,
  Title,
} from "../styled.components";
import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { useQuery } from "react-query";
import { fetchInfo, fetchPrice } from "../api";
import { Params, IPriceData } from "../atoms";
interface IRouteState {
  state: {
    name: string;
  };
}

interface IInfoData {
  id: string;
  name: string;
  symbol: string;
  rank: number;
  is_new: boolean;
  is_active: boolean;
  type: string;
  description: string;
  message: string;
  open_source: boolean;
  started_at: string;
  development_status: string;
  hardware_wallet: boolean;
  proof_type: string;
  org_structure: string;
  hash_algorithm: string;
  first_data_at: string;
  last_data_at: string;
}

function Coin() {
  const { coinId } = useParams() as Params;
  const { state } = useLocation() as IRouteState; //react router DOM이 보내주는 location object에 접근
  const priceMatch = useMatch("/:coind/price"); //해당 url 있으면 캐치
  const chartMatch = useMatch("/:coinId/chart");
  const { isLoading: infoLoading, data: info } = useQuery<IInfoData>(
    ["info", coinId],
    () => fetchInfo(coinId),
    {
      refetchInterval: 10000,
    }
  ); //fetcher 함수는 Params(coiId) 알기위해
  const { isLoading: priceLoading, data: priceInfo } = useQuery<IPriceData>(
    ["price", coinId],
    () => fetchPrice(coinId),
    {
      refetchInterval: 5000,
    }
  );
  const loading = infoLoading || priceLoading;
  /* const [loading, setLoading] = useState(true);
  const [info, setInfo] = useState<IInfoData>();
  const [priceInfo, setPriceInfo] = useState<IPriceData>(); */
  //const [priceInfo, setPriceInfo] = useState({}); Typescript에서 빈 object라 생각
  /* useEffect(() => {
    (async () => {
      const response = await fetch(
        `https://api.coinpaprika.com/v1/coins/${coinId}`
      );
      const json = await response.json();
    })();
    (async () => {
      const infoData = await (
        await fetch(`https://api.coinpaprika.com/v1/coins/${coinId}`)
      ).json(); //캡슐화
      setInfo(infoData);
      const priceData = await (
        await fetch(`https://api.coinpaprika.com/v1/tickers/${coinId}`)
      ).json();
      setPriceInfo(priceData);
      setLoading(false);
    })();
  }, [coinId]); //componentd의 시작에서만 코드 실핼 할때는 '[]' 실시간으로 coinId가 변경될시 [coinId] 명시 */
  return (
    <>
      <Container>
        <Helmet>
          <Title>
            {state?.name ? state.name : loading ? "loading..." : info?.name}
          </Title>
        </Helmet>
        <Header>
          <button>
            <Link to={"/"}>Back</Link>
          </button>
          <Title>
            {state?.name ? state.name : loading ? "loading..." : info?.name}
          </Title>
        </Header>
        {loading ? (
          <Loader>Loading...</Loader>
        ) : (
          <>
            <Overview>
              <OverviewItem>
                <span>Rank:</span>
                <span>{info?.rank}</span>
              </OverviewItem>
              <OverviewItem>
                <span>Symbol:</span>
                <span>{info?.symbol}</span>
              </OverviewItem>
              <OverviewItem>
                <span>Price:</span>
                <span>${priceInfo?.quotes.USD.price.toFixed(3)}</span>
              </OverviewItem>
            </Overview>
            <Description>{info?.description}</Description>
            <Overview>
              <OverviewItem>
                <span>Total Supply:</span>
                <span>{priceInfo?.total_supply}</span>
              </OverviewItem>
              <OverviewItem>
                <span>Max Supply</span>
                <span>{priceInfo?.max_supply}</span>
              </OverviewItem>
            </Overview>

            <Tabs>
              <Tab isAcitve={chartMatch !== null}>
                <Link to={`/${coinId}/chart`}>Chart</Link>
              </Tab>
              <Tab isAcitve={priceMatch !== null}>
                <Link to={`/${coinId}/price`}>Price</Link>
              </Tab>
            </Tabs>
            <Outlet />
          </>
        )}
      </Container>
    </>
  );
}

export default Coin;
