import { useQuery } from "react-query";
import { useParams } from "react-router-dom";
import { fetchCoinHistory } from "../api";
import { isDarkAtom, Params } from "../atoms";
import ApexChart from "react-apexcharts";
import moment from "moment";
import { useRecoilValue } from "recoil";

interface IHistory {
  time_open: string;
  time_close: string;
  open: number;
  high: number;
  low: number;
  close: number;
  volume: number;
  market_cap: number;
}

function Chart() {
  const { coinId } = useParams() as Params;
  const { isLoading, data } = useQuery<IHistory[]>(["onlcv", coinId], () =>
    fetchCoinHistory(coinId)
  );
  const isDark = useRecoilValue(isDarkAtom);
  return (
    <div>
      {isLoading ? (
        `Loading ${coinId}...`
      ) : (
        <ApexChart
          type="candlestick"
          series={[
            {
              name: "series-1",
              data: data?.map((props) => [
                new Date(props.time_open),
                props.open.toFixed(2),
                props.high.toFixed(2),
                props.low.toFixed(2),
                props.close.toFixed(2),
              ]),
            },
          ]}
          options={{
            chart: {
              type: "candlestick",
              height: 500,
              width: 500,
            },
            title: {
              text: `${coinId.toUpperCase()} Chart`,
              align: "left",
            },
            xaxis: {
              type: "datetime",
            },
            yaxis: {
              tooltip: {
                enabled: true,
              },
            },
            plotOptions: {
              candlestick: {
                colors: {
                  upward: "#3C90EB",
                  downward: "#DF7D46",
                },
              },
            },
            theme: {
              mode: isDark ? "dark" : "light",
            },
          }}
        />
      )}
    </div>
  );
}

export default Chart;
