import styled from "styled-components";
import { Helmet } from "react-helmet";
import { useQuery } from "react-query";
import { Link } from "react-router-dom";
import { useSetRecoilState } from "recoil";
import {
  Container,
  Header,
  IconImg,
  Loader,
  Title,
} from "../styled.components";
import { fetchCoins } from "../api";
import { isDarkAtom } from "../atoms";
import DarkModeToggle from "react-dark-mode-toggle";
import { useState } from "react";

const CoinsList = styled.ul``;

const Coin = styled.li`
  background-color: white;
  color: #192a56;
  padding: 20px;
  border-radius: 15px;
  margin-bottom: 10px;
  a {
    display: flex;
    align-items: center;
    padding: 20px;
    transform: color 0.2s ease-in;
    display: block;
  }
  &:hover {
    a {
      color: ${(props) => props.theme.accentColor};
    }
  }
`;

interface ICoin {
  id: string;
  name: string;
  symbol: string;
  rank: number;
  is_new: boolean;
  is_active: boolean;
  type: string;
}

function Coins() {
  const setDartAtom = useSetRecoilState(isDarkAtom);
  const [isDark, setIsDark] = useState(() => true);
  const toggleDarkAtom = () => {
    setDartAtom((isDark) => !isDark);
    setIsDark(!isDark);
  };
  const { isLoading, data } = useQuery<ICoin[]>("allCoins", fetchCoins); //useQuery("querykey",fetch function)
  // React query => response를 caching (API로 부터 response 받고 있어서 화면 변경 후 돌아와도 원하는 data가 이미 cache에 있음)
  return (
    <Container>
      <Helmet>
        <Title>COIN</Title>
      </Helmet>
      <Header>
        <Title>COIN</Title>
        <DarkModeToggle onChange={toggleDarkAtom} checked={isDark} size={50} />
        {/* <button onClick={toggleDarkAtom}>Toggle Mode</button> */}
      </Header>
      {isLoading ? (
        <Loader>Loading...</Loader>
      ) : (
        <CoinsList>
          {data?.slice(0, 100).map((coin) => (
            <Coin key={coin.id}>
              <Link to={`/${coin.id}`} state={{ name: coin.name }}>
                {/* 다른화면으로 string or state같은 정보 보낼 수 있음 */}
                <IconImg
                  src={`https://cryptoicon-api.vercel.app/api/icon/${coin.symbol.toLowerCase()}`}
                ></IconImg>
                {coin.name} &rarr;
              </Link>
            </Coin>
          ))}
        </CoinsList>
      )}
    </Container>
  );
}

export default Coins;

